import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  count: 0,
  tables: {},
  counts: {}
}

const set_helper = prop => (state, payload) => (state[prop] = payload[prop])

const mutations = {
  INCREMENT (state) {
    state.count++
  },
  DECREMENT (state) {
    state.count--
  },
  SET_TABLES: set_helper('tables'),
  SET_COUNTS: set_helper('counts')
}
const actions = {
  incrementAsync ({ commit }) {
    setTimeout(() => {
      commit('INCREMENT')
    }, 200)
  },
  setTables ({ commit }, { tables }) {
    commit('SET_TABLES', {
      tables
    })
  }
}

const store = new Vuex.Store({
  state,
  mutations,
  actions
})

export default store



