<template>
  <div class="container content">
    <router-link class="title is-4" to="/"> 🏡 返回首页</router-link>

    <hr>

    <div class="columns">
      <div class="column">
        <h5 class="title is-6">基本详情</h5>
        <ul>
          <li>最大页数 {{ maxPage }}</li>
          <li>当前页面 {{ page }}</li>
          <li>当前分页 {{ limit }}</li>
          <li class="create" @click="open=true">创建</li>
        </ul>
      </div>
      <div class="column">
        <h5 class="title is-6">分页链接</h5>
        <ul>
          <template v-for="index in (maxPage)">
            <li :key="index">
              <router-link class="btn" :key="index" :to="'/model/' + type + '/' + index"> 第 {{index}} 页 </router-link>
            </li>
          </template>
        </ul>
      </div>
    </div>
    <!-- <div class="json" v-html="pretty(displayedItems)"></div> -->
    <div class="container">
      <table class="table table__model-list">
        <thead>
          <th v-for="(_,val) in tables[type]" :key="val" v-text="val"></th>
          <th>操作</th>
        </thead>
        <tr v-for="item in displayedItems" :key="item.id">
          <td :key="text" v-for="text in item" v-text="text"></td>
          <td><i class="fas fa-trash-alt" @click="remove(item.id)"></i><i class="fas fa-edit" @click="edit(item)"></i></td>
        </tr>
      </table>
    </div>

    <vue-modaltor  :visible="open" @hide="hideModal">
      <v-form :model="type" :config="nomarlizeCreateConfig(tables[type])" :url="'/api/model/' + type" @finish="finish" :edit="editItem" />
    </vue-modaltor>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import * as R from 'ramda'
import pretty from 'json-pretty-html'
import VForm from '../components/VForm'
import { mapObjToArray } from '../utils'
import VueNotifications from 'vue-notifications'

export default {
  name: 'model-list',
  props: {
    type: String
  },
  components: {
    VForm
  },
  data() {
    return {
      limit: 20,
      displayedItems: [],
      displayedPage: 1,
      open: false,
      editItem: null
    }
  },
  computed: {
    page() {
      return Number(this.$route.params.page) || 1
    },
    maxPage() {
      const { counts } = this.$store.state
      return Math.ceil(counts[this.type] / this.limit) || 1
    },
    hasMore() {
      return this.page < this.maxPage
    },
    ...mapState(['tables'])
  },
  watch: {
    page(to, from) {
      this.loadItems(to, from)
    }
  },
  notifications: {
    showSuccessMsg: {
      type: VueNotifications.types.success,
      title: '操作成功',
      message: '完成!'
    },
    showErrorMsg: {
      type: VueNotifications.types.error,
      title: '操作失败',
      message: '空'
    }
  },
  methods: {
    hideModal() {
      this.open = false
    },
    edit(item) {
      this.editItem = item
      console.log(this.editItem)
      this.open = true
    },
    finish() {
      this.open = false
      this.editItem = null
      this.showSuccessMsg()
      this.loadItems()
    },
    async remove(id) {
      try {
        const data = await this.$api.del(this.type, id)
        if (data.status === 200) {
          this.showSuccessMsg({ message: '删除成功' })
          this.loadItems()
        }
      } catch (e) {
        this.showErrorMsg({ message: e.response.data })
      }
    },
    nomarlizeCreateConfig(config) {
      return mapObjToArray(
        (val, key) => ({
          show_name: this.$dbMap[key] || key,
          push_name: key,
          defaultValue: '',
          type: val
        }),
        config
      )
    },
    pretty,
    async loadItems(to = this.page, from = -1) {
      if (this.page < 0 || this.page > this.maxPage) {
        this.$router.replace(`/model/${this.type}/1`)
        return
      }
      this.displayedPage = to
      const where = {
        limit: this.limit
      }
      if (this.limit * (this.page - 1)) {
        where.offset = this.limit * (this.page - 1)
      }
      let { data } = await this.$api.getList(this.type, where)
      console.log(data)
      this.displayedItems = data
    }
  },
  async mounted() {
    await this.loadItems()
  }
}
</script>


<style>
.json {
  font-family: Menlo, Monaco, 'Courier New', monospace;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0;
  background-color: #24282a;
  color: #d4d4d4;
  text-align: left;
  border-top: 1px solid #121516;
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 0;
}
.json-pretty {
  padding-left: 30px;
  padding-right: 30px;
}
.json-selected {
  background-color: rgba(139, 191, 228, 0.19999999999999996);
}

.json-string {
  color: #6caedd;
}

.json-key {
  color: #ec5f67;
}

.json-boolean {
  color: #99c794;
}

.json-number {
  color: #99c794;
}

.create,
i {
  cursor: pointer;
}
.container {
  overflow: scroll;
}
.table.table__model-list {
  min-width: 200%;
}
</style>



