<template>
  <div class="page">
    <!-- <counter></counter> -->
    <!-- <v-form model="User" :config="config" url="/api/User"></v-form> -->

    <div class="container">
      <h2>Model List</h2>
      <template v-for="(_, name) in tables">
        <div :key="name" class="is-size-4">
          <router-link class="tag is-small" :to="{ path: '/model/' + name  }" v-text="name"/>
        </div>
      </template>
    </div>


    <!-- <template v-for="(config, name) in tables">
      <v-form :key="name" :model="name" :config="nomarlizeConfig(config)" :url="'/api/' + name" />
    </template> -->
  </div>
</template>

<script>
import Counter from 'components/Counter'
import VForm from 'components/VForm'
import CreateListView from './CreateListView'
import { forEachObjIndexed } from 'ramda'
import { mapState } from 'vuex'

export default {
  components: {
    VForm
  },
  computed: {
    ...mapState(['tables'])
  },
  methods: {
    nomarlizeConfig(config) {
      const array = []
      forEachObjIndexed((val, key) => {
        array.push({
          show_name: key,
          push_name: key,
          defaultValue: '',
          type: val
        })
      }, config)
      return array
    }
  },
  async mounted() {
    const { data: tables } = await this.$api.getConfig()
    const { data: counts } = await this.$api.getCount()
    this.$store.commit('SET_TABLES', { tables })
    this.$store.commit('SET_COUNTS', { counts })
  }
}
</script>



