import { forEachObjIndexed } from 'ramda'

export const mapObjToArray = (fn, obj) => {
  const arr = []
  forEachObjIndexed((val, key) => {
    arr.push(fn(val, key, obj))
  }, obj)
  return arr
}

export const mapObj = (fn, obj) => {
  const _obj = []
  forEachObjIndexed((val, key) => {
    const _ret = fn(val, key, obj)
    obj[key] = _ret
  }, obj)
  return _obj
}



