import axios from 'axios'

const baseURL = 'http://localhost:7001/api/v1/admin'

export function getConfig () {
  return axios.get(baseURL + '_table', {
    auth: {
      username: 'dd',
      password: '888888'
    }
  })
}

export function getCount () {
  return axios.get(baseURL + '_count', {
    auth: {
      username: 'dd',
      password: '888888'
    }
  })
}

const fetch = axios.create({
  baseURL,
  auth: {
    username: 'dd',
    password: '888888'
  }
})

export function getList (model, query) {
  return fetch.get(model, {
    params: query
  })
}

export function create (model, data) {
  return fetch({
    method: 'POST',
    url: `/${model}`,
    data
  })
}

export function update (model, id, data) {
  return fetch({
    method: 'PUT',
    url: `/${model}/${id}`,
    data
  })
}

export function del (model, id, data) {
  return fetch({
    method: 'DELETE',
    url: `/${model}/${id}`,
    data
  })
}



