import Vue from 'vue'
import { debounce, map, pick } from 'lodash'
import VueNotifications from 'vue-notifications'

//* need config sample
/* eslint-disable */
const configSample = [
  {
    push_name: 'username',
    show_name: '用户名',
    defaultValue: '',
    type: 'string'
  }
]
/* eslint-enable */

const VForm = Vue.component('v-form', {
  props: {
    model: {
      type: String,
      required: true
    },
    url: {
      type: String,
      required: true
    },
    config: {
      type: Array,
      required: true
    },
    edit: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      //* init the config { push_name -> defaultValue }
      ...this.config.reduce((acc, { push_name, defaultValue }) => {
        acc[push_name] = defaultValue || ''
        return acc
      }, {})
    }
  },
  notifications: {
    showSuccessMsg: {
      type: VueNotifications.types.success,
      title: '操作成功',
      message: '数据创建完成!'
    },
    showErrorMsg: {
      type: VueNotifications.types.error,
      title: '操作失败',
      message: '空'
    }
  },
  computed: {
    fieldsNames() {
      return map(this.config, 'push_name')
    }
  },
  watch: {
    edit(newVal) {
      Object.assign(this, newVal)
    }
  },
  methods: {
    //! onKeyUp bind fn, use v-model replace it.
    change(event) {
      ~debounce(() => {
        this.$set(this, event.target.name, event.target.value)
      }, 520)()
    },
    handleRowData() {
      return pick(this, this.fieldsNames)
    },
    pushMethod(model, data) {
      return this.edit ? this.$api.update(model, data.id, data) : this.$api.create(model, data)
    },
    resets() {
      this.fieldsNames.map(v => {
        this[v] = ''
      })
    },
    async push(e) {
      e.preventDefault()
      try {
        const push_row_data = this.handleRowData()
        const { data: model } = await this.pushMethod(this.model, push_row_data)
        if (model) {
          this.resets()
          this.$emit('finish')
        }
      } catch (e) {
        this.showErrorMsg({
          message: e.response.data
        })
      }
    }
  },
  render() {
    //* build forms
    const forms = this.config.map(({ push_name, type, defaultValue, show_name }) => {
      switch (type) {
        case 'textarea':
          return (
            <div class="field">
              <label class="label">{show_name}</label>
              <div class="control">
                <textarea class="textarea" name={push_name} v-model={this[push_name]}>
                  {defaultValue}
                </textarea>
              </div>
            </div>
          )
        default:
          return (
            <div class="field">
              <label class="label">{show_name}</label>
              <div class="control">
                <input
                  class="input"
                  domPropsType={type}
                  name={push_name}
                  v-model={this[push_name]}
                  placeholder={show_name}
                />
              </div>
            </div>
          )
      }
    })
    //* render jsx
    return (
      <div class="form">
        <h2>{this.model}</h2>
        {this.data}
        <form>
          {forms}
          <div class="field ">
            <div class="control">
              <button class="button is-primary" onClick={this.push}>
                提交数据
              </button>
            </div>
          </div>
        </form>
      </div>
    )
  }
})

export default VForm



